B;EAC9B,4CAA4C;AAC9C;;AAEA;EACE,4BAA4B;EAC5B,4CAA4C;AAC9C;;AAEA;EACE,4BAA4B;EAC5B,4CAA4C;AAC9C;;AAEA;EACE,6BAA6B;EAC7B,4CAA4C;AAC9C;;AAEA;EACE,2CAA2C;AAC7C;;AAEA;EACE,wCAAwC;AAC1C;;AAEA,yBAAyB;AACzB;EACE,qCAAqC;EACrC,oCAAoC;EACpC,kCAAkC;AACpC;;AAEA;EACE,uBAAuB;AACzB;;AAEA;EACE,0CAA0C;EAC1C,yBAAyB;AAC3B;;AAEA,mCAAmC;AACnC;EACE;IACE,4CAA4C;EAC9C;EACA;IACE,4CAA4C;EAC9C;EACA;IACE,4CAA4C;EAC9C;AACF;;AAEA;;;;EAIE,iCAAiC;AACnC;;AAEA;EACE,iDAAiD;AACnD;;AAEA,wBAAwB;AACxB;EACE,eAAe;EACf,SAAS;EACT,WAAW;EACX,aAAa;EACb,0CAA0C;AAC5C;;AAEA;EACE,0CAA0C;AAC5C;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,kBAAkB;EAClB,mCAAmC;EACnC,mBAAmB;EACnB,8CAA8C;EAC9C,YAAY;EACZ,gBAAgB;AAClB;;AAEA;EACE,iBAAiB;EACjB,kBAAkB;EAClB,oBAAoB;AACtB;;AAEA;EACE,gBAAgB;EAChB,gBAAgB;AAClB;;AAEA;EACE,SAAS;AACX;;AAEA,sCAAsC","sourcesContent":["/* Gamified Theme for SFManager - Modern Dark Edition */\n\n:root {\n  /* Base colors */\n  --primary: #4a6cf7;\n  --primary-dark: #1e3cdf;\n  --secondary: #6c757d;\n  --success: #198754;\n  --info: #0dcaf0;\n  --warning: #ffc107;\n  --danger: #dc3545;\n  --light: #f8f9fa;\n  --dark: #212529;\n  \n  /* Light mode */\n  --dark: #1A1A2E;\n  --light: #F1F2F6;\n  --background: #FAFAFA;\n  --card-bg: #FFFFFF;\n  --text: #2F3542;\n  --text-light: #747D8C;\n  \n  /* Gradients */\n  --gradient-primary: linear-gradient(135deg, #4a6cf7 0%, #1e3cdf 100%);\n  --gradient-secondary: linear-gradient(135deg, #6c757d 0%, #495057 100%);\n  --gradient-accent: linear-gradient(135deg, #FFA502 0%, #FF7102 100%);\n  --gradient-success: linear-gradient(135deg, #198754 0%, #00BD59 100%);\n  --gradient-danger: linear-gradient(135deg, #FF2D5E 0%, #FF0844 100%);\n  \n  /* Dark mode colors */\n  --dark-background: #0f172a;\n  --dark-card-bg: #1e293b;\n  --dark-secondary-bg: #1e293b;\n  --dark-border: #334155;\n  --dark-text: #f8fafc;\n  --dark-text-light: #cbd5e1;\n  \n  /* Dark mode gradients */\n  --dark-gradient-bg: linear-gradient(135deg, #0F0F1A 0%, #1A1A2E 100%);\n  --dark-gradient-card: linear-gradient(135deg, #1A1A2E 0%, #16213E 100%);\n  \n  /* Neon colors */\n  --neon-purple: #a761ff;\n  --neon-blue: #3ed8ff;\n  --neon-pink: #ff2d5e;\n  --neon-green: #19ffb2;\n  --neon-yellow: #ffdd3a;\n  \n  /* Neon gradients */\n  --neon-gradient-primary: linear-gradient(135deg, var(--neon-blue) 0%, var(--neon-purple) 100%);\n  --neon-gradient-secondary: linear-gradient(135deg, var(--neon-purple) 0%, var(--neon-pink) 100%);\n  --neon-gradient-success: linear-gradient(135deg, var(--neon-green) 0%, var(--neon-blue) 100%);\n  --neon-gradient-warning: linear-gradient(135deg, var(--neon-yellow) 0%, var(--neon-pink) 100%);\n  \n  /* UI elements */\n  --border-radius: 10px;\n  --border-radius-lg: 15px;\n  --border-radius-sm: 5px;\n  --box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);\n  --dark-box-shadow: 0 8px 30px rgba(0, 0, 0, 0.3);\n  --neon-shadow: 0 0 20px rgba(167, 97, 255, 0.5);\n  \n  /* RGB Values for CSS Variables */\n  --neon-purple-rgb: 167, 97, 255;\n  --neon-blue-rgb: 62, 216, 255;\n  --neon-pink-rgb: 255, 45, 94;\n  --neon-green-rgb: 25, 255, 178;\n  --neon-yellow-rgb: 255, 221, 58;\n}\n\n/* Body and general styles */\nbody {\n  font-family: 'Poppins', 'Segoe UI', sans-serif;\n  background-color: #f5f7fb;\n  color: var(--dark);\n  padding-top: 70px;\n  transition: background-color 0.3s ease, color 0.3s ease;\n}\n\n.container {\n  padding-top: 20px;\n  padding-bottom: 30px;\n}\n\n/* Navbar Gamified */\n.navbar-gamified {\n  background: var(--gradient-primary);\n  padding: 12px 0;\n  box-shadow: 0 4px 12px rgba(108, 92, 231, 0.2);\n  position: fixed;\n  top: 0;\n  left: 0;\n  right: 0;\n  z-index: 1030;\n  transition: all 0.3s ease;\n}\n\n.navbar-brand {\n  font-weight: 700;\n  font-size: 1.5rem;\n  color: white !important;\n  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.2);\n  transition: transform 0.3s ease;\n}\n\n.navbar-brand:hover {\n  transform: scale(1.05);\n}\n\n.navbar-brand i {\n  color: var(--accent);\n  margin-right: 8px;\n}\n\n.navbar-nav .nav-link {\n  color: rgba(255, 255, 255, 0.85) !important;\n  font-weight: 500;\n  padding: 10px 15px;\n  border-radius: var(--border-radius);\n  transition: all 0.3s ease;\n  margin: 0 5px;\n}\n\n.navbar-nav .nav-link:hover {\n  color: white !important;\n  background-color: rgba(255, 255, 255, 0.1);\n  transform: translateY(-2px);\n}\n\n.navbar-nav .nav-link.active {\n  color: white !important;\n  background-color: rgba(255, 255, 255, 0.15);\n  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);\n}\n\n.navbar-gamified .dropdown-menu {\n  border-radius: var(--border-radius);\n  box-shadow: var(--box-shadow);\n  border: none;\n  padding: 12px 0;\n  margin-top: 12px;\n  background-color: var(--card-bg);\n}\n\n.navbar-gamified .dropdown-item {\n  padding: 10px 20px;\n  font-weight: 500;\n  transition: all 0.2s;\n  color: var(--text);\n}\n\n.navbar-gamified .dropdown-item:hover {\n  background-color: var(--light);\n  transform: translateX(5px);\n}\n\n.navbar-gamified .dropdown-toggle {\n  border-radius: 50px;\n  padding: 8px 20px;\n  background: rgba(255, 255, 255, 0.15);\n  border: none;\n  font-weight: 600;\n  display: flex;\n  align-items: center;\n  color: white !important;\n  transition: all 0.3s;\n}\n\n.navbar-gamified .dropdown-toggle:hover, \n.navbar-gamified .dropdown-toggle:focus {\n  background: rgba(255, 255, 255, 0.25);\n  transform: translateY(-2px);\n}\n\n.navbar-gamified .dropdown-toggle::after {\n  margin-left: 10px;\n}\n\n.navbar-gamified .dropdown-toggle i {\n  margin-right: 8px;\n  font-size: 1.2rem;\n}\n\n/* Avatar style */\n.user-avatar {\n  width: 32px;\n  height: 32px;\n  border-radius: 50%;\n  background: var(--neon-gradient-accent);\n  color: white;\n  display: inline-flex;\n  align-items: center;\n  justify-content: center;\n  font-weight: 700;\n  margin-right: 8px;\n}\n\n/* Buttons */\n.btn {\n  border-radius: 50px;\n  padding: 10px 25px;\n  font-weight: 600;\n  transition: all 0.3s;\n  border: none;\n  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);\n}\n\n.btn:hover {\n  transform: translateY(-3px);\n  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.15);\n}\n\n.btn:active {\n  transform: translateY(1px);\n}\n\n.btn-primary {\n  background: var(--gradient-primary);\n  color: white;\n}\n\n.btn-secondary {\n  background: var(--gradient-secondary);\n  color: white;\n}\n\n.btn-accent {\n  background: var(--gradient-accent);\n  color: white;\n}\n\n.btn-success {\n  background: var(--gradient-success);\n  color: white;\n}\n\n.btn-danger {\n  background: var(--gradient-danger);\n  color: white;\n}\n\n.btn-outline-primary {\n  border: 2px solid var(--primary);\n  color: var(--primary);\n  background: transparent;\n}\n\n.btn-outline-primary:hover {\n  background-color: var(--primary);\n  color: white;\n}\n\n/* Cards */\n.card {\n  border-radius: var(--border-radius);\n  border: none;\n  overflow: hidden;\n  box-shadow: var(--box-shadow);\n  transition: all 0.3s ease;\n  margin-bottom: 20px;\n  background-color: var(--card-bg);\n}\n\n.card:hover {\n  transform: translateY(-5px);\n  box-shadow: 0 12px 30px rgba(0, 0, 0, 0.15);\n}\n\n.card-header {\n  background: var(--gradient-primary);\n  color: white;\n  font-weight: 600;\n  border-bottom: none;\n  padding: 15px 20px;\n}\n\n.card-body {\n  padding: 20px;\n}\n\n/* Badges */\n.badge {\n  padding: 6px 12px;\n  font-weight: 600;\n  border-radius: 50px;\n}\n\n.badge-primary {\n  background: var(--primary);\n  color: white;\n}\n\n.badge-secondary {\n  background: var(--secondary);\n  color: white;\n}\n\n.badge-accent {\n  background: var(--accent);\n  color: white;\n}\n\n.badge-success {\n  background: var(--success);\n  color: white;\n}\n\n.badge-danger {\n  background: var(--danger);\n  color: white;\n}\n\n/* Points Display */\n.points-display {\n  display: flex;\n  align-items: center;\n  background: rgba(255, 255, 255, 0.1);\n  padding: 6px 12px;\n  border-radius: 50px;\n  color: white;\n  font-weight: 600;\n  margin-right: 15px;\n}\n\n.points-display i {\n  color: var(--neon-yellow);\n  margin-right: 6px;\n}\n\n.lev