  color: #6c757d !important;
}

/* Fix for analytics charts with white backgrounds */
body.dark-theme .chart-area,
body.dark-theme .chart-canvas,
body.dark-theme canvas {
  background-color: transparent;
}

body.dark-theme .white-card {
  background-color: #fff !important;
  color: #212529 !important;
}

body.dark-theme .white-card h1,
body.dark-theme .white-card h2,
body.dark-theme .white-card h3,
body.dark-theme .white-card h4,
body.dark-theme .white-card h5,
body.dark-theme .white-card h6,
body.dark-theme .white-card p,
body.dark-theme .white-card span,
body.dark-theme .white-card div,
body.dark-theme .white-card a:not(.btn) {
  color: #212529 !important;
}

body.dark-theme .white-card .text-muted {
  color: #6c757d !important;
}

/* Fix for analytics widgets */
body.dark-theme .widget-white,
body.dark-theme .bg-white {
  background-color: #fff !important;
  color: #212529 !important;
}

body.dark-theme .widget-white *,
body.dark-theme .bg-white * {
  color: #212529 !important;
}

body.dark-theme .widget-white .text-muted,
body.dark-theme .bg-white .text-muted {
  color: #6c757d !important;
}

/* Ensure text is readable on light backgrounds in dark theme */
body.dark-theme [style*="background-color: #fff"],
body.dark-theme [style*="background-color: white"],
body.dark-theme [style*="background-color: rgb(255, 255, 255)"],
body.dark-theme [style*="background-color: rgba(255, 255, 255"] {
  color: #212529 !important;
}

body.dark-theme [style*="background-color: #fff"] *,
body.dark-theme [style*="background-color: white"] *,
body.dark-theme [style*="background-color: rgb(255, 255, 255)"] *,
body.dark-theme [style*="background-color: rgba(255, 255, 255"] * {
  color: #212529 !important;
}

/* Futuristic UI Elements for startup detail page */
.action-buttons-container {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-bottom: 20px;
}

.btn-with-icon {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  border-radius: 50px;
  transition: all 0.3s ease;
}

body.dark-theme .btn-with-icon {
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

body.dark-theme .btn-with-icon:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 20px rgba(var(--neon-purple-rgb), 0.3);
}

.btn-with-icon i {
  font-size: 1.1rem;
}

.with-hover-effect {
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.with-hover-effect:hover {
  transform: translateY(-5px);
}

body.dark-theme .with-hover-effect {
  background: rgba(30, 41, 59, 0.8);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(var(--neon-blue-rgb), 0.1);
}

body.dark-theme .with-hover-effect:hover {
  box-shadow: 0 10px 25px rgba(var(--neon-blue-rgb), 0.2);
  border: 1px solid rgba(var(--neon-blue-rgb), 0.2);
}

.founder-avatar {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  background: var(--gradient-primary);
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.8rem;
  font-weight: 700;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

body.dark-theme .founder-avatar {
  background: var(--neon-gradient-primary);
  box-shadow: 0 0 20px rgba(var(--neon-purple-rgb), 0.3);
}

.founder-badge {
  background: linear-gradient(to right, #FFD700, #FFA500);
  color: #000;
  font-weight: 600;
  padding: 5px 12px;
  border-radius: 20px;
}

body.dark-theme .founder-badge {
  background: linear-gradient(to right, var(--neon-yellow), var(--neon-pink));
  color: white;
  box-shadow: 0 0 10px rgba(var(--neon-yellow-rgb), 0.3);
}

.team-member-card {
  display: flex;
  align-items: center;
  padding: 15px;
  border-radius: 12px;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(8px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: all 0.3s ease;
  height: 100%;
}

.team-member-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
}

body.dark-theme .team-member-card {
  background: rgba(15, 23, 42, 0.5);
  border: 1px solid rgba(var(--neon-blue-rgb), 0.1);
}

body.dark-theme .team-member-card:hover {
  box-shadow: 0 10px 20px rgba(var(--neon-blue-rgb), 0.15);
  border: 1px solid rgba(var(--neon-blue-rgb), 0.2);
}

.team-member-avatar {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: var(--secondary);
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.2rem;
  font-weight: 600;
  margin-right: 15px;
}

body.dark-theme .team-member-avatar {
  background: linear-gradient(135deg, var(--neon-blue), var(--neon-green));
  box-shadow: 0 0 15px rgba(var(--neon-blue-rgb), 0.2);
}

.team-member-info {
  flex: 1;
}

.role-type-badge {
  background: var(--secondary);
  color: white;
  padding: 4px 10px;
  border-radius: 20px;
  font-size: 0.75rem;
  font-weight: 500;
}

body.dark-theme .role-type-badge {
  background: rgba(var(--neon-blue-rgb), 0.2);
  border: 1px solid rgba(var(--neon-blue-rgb), 0.3);
  color: var(--neon-blue);
}

.website-link {
  color: var(--primary);
  text-decoration: none;
  transition: all 0.3s ease;
}

.website-link:hover {
  color: var(--primary-dark);
}

body.dark-theme .website-link {
  color: var(--neon-blue);
}

body.dark-theme .website-link:hover {
  color: var(--neon-purple);
}

.external-link-icon {
  opacity: 0;
  transform: translateX(-5px);
  transition: all 0.3s ease;
}

.website-link:hover .external-link-icon {
  opacity: 1;
  transform: translateX(0);
} `, "",{"version":3,"sources":["webpack://./src/client/styles.css"],"names":[],"mappings":"AAAA;EACE,4DAA4D;EAC5D,yBAAyB;EACzB,iBAAiB;AACnB;;AAEA;EACE,mBAAmB;EACnB,gBAAgB;EAChB,mBAAmB;EACnB,wCAAwC;EACxC,yBAAyB;AAC3B;;AAEA;EACE,6DAA6D;EAC7D,YAAY;EACZ,aAAa;AACf;;AAEA;;EAEE,qBAAqB;EACrB,kDAAkD;AACpD;;AAEA;EACE,6DAA6D;EAC7D,YAAY;EACZ,yBAAyB;AAC3B;;AAEA;EACE,6DAA6D;EAC7D,2BAA2B;AAC7B;;AAEA;EACE,yBAAyB;EACzB,cAAc;EACd,iBAAiB;EACjB,mBAAmB;EACnB,iBAAiB;EACjB,iBAAiB;EACjB,kBAAkB;EAClB,qBAAqB;AACvB;;AAEA;EACE,kBAAkB;EAClB,iCAAiC;EACjC,mBAAmB;EACnB,gBAAgB;EAChB,mBAAmB;EACnB,eAAe;EACf,YAAY;EACZ,+CAA+C;EAC/C,0BAA0B;EAC1B,2CAA2C;EAC3C,4DAA4D;AAC9D;;AAEA;EACE,iCAAiC;EACjC,iDAAiD;AACnD;;AAEA;EACE,uCAAuC;EACvC,8DAA8D;AAChE;;AAEA;EACE,qDAAqD;AACvD;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,YAAY;EACZ;;gCAE8B;EAC9B,aAAa;AACf;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,kBAAkB;EAClB,gBAAgB;EAChB,mBAAmB;EACnB,oCAAoC;EACpC,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,iDAAiD;EACjD,oDAAoD;AACtD;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,iBAAiB;AACnB;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,iFAAiF;EACjF,YAAY;EACZ,eAAe;EACf,gBAAgB;AAClB;;AAEA;EACE,iBAAiB;EACjB,gBAAgB;EAChB,kBAAkB;EAClB,YAAY;EACZ,uDAAuD;AACzD;;AAEA;EACE,mBAAmB;AACrB;;AAEA;EACE,qBAAqB;EACrB,iBAAiB;EACjB,mBAAmB;EACnB,iBAAiB;EACjB,gBAAgB;EAChB,2EAA2E;EAC3E,YAAY;EACZ,6CAA6C;EAC7C,sDAAsD;AACxD;;AAEA;EACE,+BAA+B;EAC/B,mBAAmB;EACnB,gBAAgB;EAChB,YAAY;AACd;;AAEA;EACE,UAAU;EACV,2BAA2B;EAC3B,oCAAoC;AACtC;;AAEA;EACE,aAAa;EACb,eAAe;EACf,QAAQ;EACR,mBAAmB;AACrB;;AAEA;EACE,qBAAqB;EACrB,iBAAiB;EACjB,mBAAmB;EACnB,kBAAkB;EAClB,oCAAoC;EACpC,+BAA+B;EAC/B,kDAAkD;AACpD;;AAEA;EACE,6CAA6C;EAC7C,mDAAmD;AACrD;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,QAAQ;EACR,WAAW;EACX,iBAAiB;EACjB,YAAY;EACZ,kBAAkB;EAClB,2EAA2E;EAC3E,YAAY;EACZ,gBAAgB;EAChB,yBAAyB;EACzB,kBAAkB;EAClB,gBAAgB;AAClB;;AAEA;EACE,WAAW;EACX,kBAAkB;EAClB,SAAS;EACT,UAAU;EACV,WAAW;EACX,YAAY;EACZ;;;;;GAKC;EACD,wBAAwB;EACxB,yDAAyD;AAC3D;;AAEA;EACE,yCAAyC;AAC3C;;AAEA;EACE,+BAA+B;AACjC;;AAEA;EACE,0BAA0B;AAC5B;;AA