ow: 0 0 0 0.25rem rgba(74, 108, 247, 0.25);\n}\n\n.btn-primary {\n  background: linear-gradient(135deg, #4a6cf7 0%, #1e3cdf 100%);\n  border: none;\n  transition: all 0.3s ease;\n}\n\n.btn-primary:hover {\n  background: linear-gradient(135deg, #1e3cdf 0%, #0a1a8f 100%);\n  transform: translateY(-2px);\n}\n\n.role-badge {\n  background-color: #e9ecef;\n  color: #212529;\n  font-size: 0.8rem;\n  border-radius: 20px;\n  padding: 5px 10px;\n  margin-right: 5px;\n  margin-bottom: 5px;\n  display: inline-block;\n}\n\n.startup-card {\n  position: relative;\n  background: rgba(15, 23, 42, 0.6);\n  border-radius: 16px;\n  overflow: hidden;\n  margin-bottom: 25px;\n  cursor: pointer;\n  height: 100%;\n  box-shadow: 0 10px 25px -5px rgba(0, 0, 0, 0.2);\n  backdrop-filter: blur(8px);\n  border: 1px solid rgba(255, 255, 255, 0.05);\n  transition: all 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);\n}\n\nbody.dark-theme .startup-card {\n  background: rgba(13, 17, 23, 0.8);\n  border: 1px solid rgba(var(--neon-blue-rgb), 0.1);\n}\n\n.startup-card:hover {\n  transform: translateY(-8px) scale(1.01);\n  box-shadow: 0 20px 30px -8px rgba(var(--neon-purple-rgb), 0.3);\n}\n\nbody.dark-theme .startup-card:hover {\n  box-shadow: 0 0 30px rgba(var(--neon-blue-rgb), 0.25);\n}\n\n.startup-card-inner {\n  display: flex;\n  flex-direction: column;\n  height: 100%;\n  background: linear-gradient(135deg, \n    rgba(255, 255, 255, 0.05) 0%, \n    rgba(255, 255, 255, 0) 100%);\n  padding: 20px;\n}\n\n.startup-logo-container {\n  width: 80px;\n  height: 80px;\n  border-radius: 50%;\n  overflow: hidden;\n  margin-bottom: 15px;\n  background: rgba(255, 255, 255, 0.1);\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  border: 2px solid rgba(var(--neon-blue-rgb), 0.3);\n  box-shadow: 0 0 15px rgba(var(--neon-blue-rgb), 0.2);\n}\n\n.startup-logo {\n  width: 100%;\n  height: 100%;\n  object-fit: cover;\n}\n\n.startup-fallback-logo {\n  width: 100%;\n  height: 100%;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  background: linear-gradient(135deg, var(--neon-purple) 0%, var(--neon-blue) 100%);\n  color: white;\n  font-size: 2rem;\n  font-weight: 700;\n}\n\n.startup-name {\n  font-size: 1.5rem;\n  font-weight: 700;\n  margin-bottom: 5px;\n  color: white;\n  text-shadow: 0 0 10px rgba(var(--neon-purple-rgb), 0.5);\n}\n\n.startup-stage {\n  margin-bottom: 15px;\n}\n\n.stage-badge {\n  display: inline-block;\n  padding: 4px 12px;\n  border-radius: 20px;\n  font-size: 0.8rem;\n  font-weight: 600;\n  background: linear-gradient(to right, var(--neon-blue), var(--neon-purple));\n  color: white;\n  text-shadow: 0 0 5px rgba(255, 255, 255, 0.5);\n  box-shadow: 0 0 10px rgba(var(--neon-purple-rgb), 0.3);\n}\n\n.startup-description {\n  color: rgba(255, 255, 255, 0.7);\n  margin-bottom: 20px;\n  line-height: 1.5;\n  flex-grow: 1;\n}\n\n.startup-card-hover-info {\n  opacity: 0;\n  transform: translateY(20px);\n  animation: fadeSlideUp 0.3s forwards;\n}\n\n.role-badge-container {\n  display: flex;\n  flex-wrap: wrap;\n  gap: 8px;\n  margin-bottom: 15px;\n}\n\n.role-badge {\n  display: inline-block;\n  padding: 5px 10px;\n  border-radius: 20px;\n  font-size: 0.75rem;\n  background: rgba(255, 255, 255, 0.1);\n  color: rgba(255, 255, 255, 0.9);\n  border: 1px solid rgba(var(--neon-green-rgb), 0.2);\n}\n\n.more-roles {\n  background: rgba(var(--neon-purple-rgb), 0.2);\n  border: 1px solid rgba(var(--neon-purple-rgb), 0.3);\n}\n\n.view-details-btn {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  gap: 8px;\n  width: 100%;\n  padding: 8px 15px;\n  border: none;\n  border-radius: 8px;\n  background: linear-gradient(to right, var(--neon-blue), var(--neon-purple));\n  color: white;\n  font-weight: 600;\n  transition: all 0.3s ease;\n  position: relative;\n  overflow: hidden;\n}\n\n.view-details-btn::after {\n  content: '';\n  position: absolute;\n  top: -50%;\n  left: -50%;\n  width: 200%;\n  height: 200%;\n  background: linear-gradient(\n    rgba(255, 255, 255, 0),\n    rgba(255, 255, 255, 0),\n    rgba(255, 255, 255, 0.3),\n    rgba(255, 255, 255, 0)\n  );\n  transform: rotate(30deg);\n  transition: transform 0.5s cubic-bezier(0.19, 1, 0.22, 1);\n}\n\n.view-details-btn:hover::after {\n  transform: translateX(100%) rotate(30deg);\n}\n\n.view-details-btn i {\n  transition: transform 0.3s ease;\n}\n\n.view-details-btn:hover i {\n  transform: translateX(4px);\n}\n\n@keyframes fadeSlideUp {\n  from {\n    opacity: 0;\n    transform: translateY(20px);\n  }\n  to {\n    opacity: 1;\n    transform: translateY(0);\n  }\n}\n\n/* Startup detail page enhancements */\n.startup-detail-header {\n  position: relative;\n  padding: 30px;\n  border-radius: 16px;\n  margin-bottom: 30px;\n  background: rgba(15, 23, 42, 0.7);\n  backdrop-filter: blur(8px);\n  border: 1px solid rgba(255, 255, 255, 0.05);\n  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.2);\n  overflow: hidden;\n}\n\nbody.dark-theme .startup-detail-header {\n  background: rgba(13, 17, 23, 0.8);\n  border: 1px solid rgba(var(--neon-blue-rgb), 0.1);\n}\n\n.startup-detail-header::before {\n  content: '';\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  background: linear-gradient(135deg, \n    rgba(var(--neon-purple-rgb), 0.1) 0%, \n    rgba(var(--neon-blue-rgb), 0.1) 100%);\n  z-index: -1;\n}\n\n.startup-detail-logo {\n  width: 100px;\n  height: 100px;\n  border-radius: 50%;\n  overflow: hidden;\n  border: 3px solid rgba(var(--neon-blue-rgb), 0.5);\n  box-shadow: 0 0 20px rgba(var(--neon-blue-rgb), 0.3);\n}\n\n.startup-banner-container {\n  position: relative;\n  border-radius: 16px;\n  overflow: hidden;\n  margin-bottom: 30px;\n  height: 300px;\n  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.3);\n}\n\n.startup-banner-image {\n  width: 100%;\n  height: 100%;\n  object-fit: cover;\n  transform: scale(1.01);\n  transition: transform 0.5s ease;\n}\n\n.startup-banner-container:hover .startup-banner-image {\n  transfor