n  from { opacity: 0; }\n  to { opacity: 1; }\n}\n\n/* Additional Modal Styles */\n.modal.show {\n  display: block !important;\n}\n\n.modal {\n  position: fixed;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  z-index: 1050;\n  overflow-x: hidden;\n  overflow-y: auto;\n  outline: 0;\n}\n\n.modal-dialog {\n  position: relative;\n  margin: 1.75rem auto;\n  max-width: 500px;\n  transition: transform 0.3s ease-out;\n}\n\n.modal-dialog-centered {\n  display: flex;\n  align-items: center;\n  min-height: calc(100% - 3.5rem);\n}\n\n.modal.fade .modal-dialog {\n  transform: translate(0, -50px);\n}\n\n.modal.show .modal-dialog {\n  transform: none;\n}\n\n.modal .modal-content {\n  position: relative;\n  display: flex;\n  flex-direction: column;\n  width: 100%;\n  border-radius: var(--border-radius);\n  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);\n  outline: 0;\n  border: 0;\n}\n\n.modal-backdrop {\n  position: fixed;\n  top: 0;\n  left: 0;\n  z-index: 1040;\n  width: 100vw;\n  height: 100vh;\n  background-color: #000;\n}\n\n.modal-backdrop.fade {\n  opacity: 0;\n}\n\n.modal-backdrop.show {\n  opacity: 0.5;\n}\n\n.fade {\n  transition: opacity 0.15s linear;\n}\n\n.fade.show {\n  opacity: 1;\n}\n\nbody.modal-open .modal {\n  overflow-x: hidden;\n  overflow-y: auto;\n}\n\n/* Rating Stars Styles */\n.rating-stars {\n  display: flex;\n  align-items: center;\n  margin-left: 10px;\n}\n\nbody.dark-theme .rating-stars {\n  background: rgba(167, 97, 255, 0.15);\n  padding: 5px 12px;\n  border-radius: 50px;\n}\n\n.rating-stars i {\n  margin-right: 2px;\n  color: var(--accent);\n}\n\nbody.dark-theme .rating-stars i {\n  color: var(--neon-yellow);\n}\n\n.rating-stars i:last-of-type {\n  margin-right: 0;\n}\n\n/* Neon effect for important buttons in dark mode */\nbody.dark-theme .btn-lg {\n  position: relative;\n}\n\nbody.dark-theme .btn-lg::after {\n  content: '';\n  position: absolute;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  border-radius: 50px;\n  z-index: -1;